<template>
  <div class="signed">

    <div class="row signedList">
      <div class="signedListInfo">
        <span class="col_333 ListInfoL">缴费流水号</span>
        <span class="ListInfoR">
            <select v-model="seleInit">
              <option  :value="sel.rownum" v-for="sel in signedArr">{{sel.payId}}</option>
            </select>
        </span>
      </div>
    </div>
    <div v-for="item in signedArr" v-if="item.rownum == seleInit">
      <div class="row signedList">
        <div class="signedListInfo">
          <span class="col_333 ListInfoL">缴费编号</span>
          <span class="ListInfoR">{{item.feeId}}</span>
        </div>
      </div>
      <div class="row signedList">
        <div class="signedListInfo">
          <span class="col_333 ListInfoL">应缴金额</span>
          <span class="ListInfoR">{{item.totalAmt}}</span>
        </div>
      </div>
      <div class="row signedList">
        <div class="signedListInfo">
          <span class="col_333 ListInfoL">缴费人员</span>
          <span class="ListInfoR">{{item.feeUserName}}</span>
        </div>
      </div>
      <div class="row signedList">
        <div class="signedListInfo">
          <span class="col_333 ListInfoL">缴费单位</span>
          <span class="ListInfoR">{{item.corpName}}</span>
        </div>
      </div>

      <div class="row signedList" @click="SelInfo">
        <div class="applyListInfo">
          <span class="col_333 ListInfoL">支付方式</span>
          <span class="ListInfoR selPayWay">{{SelPayWay}}</span>
          <i class="icon iconfont icon-PayWay">&#xe6d0;</i>
        </div>
      </div>

      <input type="button" @click="signedBtn(item)" value="立即缴费" class="signedBtn" style="background: #ff4444">

      <!--弹出层-->

      <div id="modal-overlay">
        <div class="modal-data">
          <div class="modal-header row">
            <div class="col tl fs_12 col_000">更换付款方式</div>
            <div class="col col-20 tr" @click="overlay">
              <img src="../../../static/image/close.png">
            </div>
          </div>
          <div class="modal-content">
            <label class="row" v-for="(item, index) in selOptions" :id="item.cardSeq"
                   :class="{'noBorder': index == selOptions.length-1}"  @click.prevent="selectType(item)">
              <div class="col tl col-10">
                <i class="icon iconfont icon-bank"></i>
              </div>
              <div class="col ">
                中国银行借记卡 (尾号{{item.cardNo.slice(-4)}})
              </div>
              <div class="col radio_box tr col-10">
                <input type="radio" :id="item.cardSeq" name="radio" :value="item.cardSeq" v-model="pick">
                <label :for="item.cardSeq"></label>
              </div>
            </label>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';

    export default {
      name: "my-info_pay-detail",

      data(){
        return {
          id: '',
          seleInit: 1,     //默认情况选中第一个
          seleObj: {},


          signedArr: [],
          SelPayWay:'请选择',
          pick: '',
          selOptions: [],
          selectCard: {
            cardNo: '',
            cardSeq: ''
          }
        }
      },

      methods:{
        signedBtn: function () {
          if(!this.selOptions.length){
            MessageBox('提示', '您暂未绑卡，请到首页进行绑卡操作哦～');
            return;
          }

          if(this.validTest()){
            this.$router.push({path:'/pay/payingNow', query: this.selectCard})
          }
        },

        selectType: function (item) {
          this.overlay();
          this.SelPayWay = '中国银行借记卡 (尾号' + item.cardNo.slice(-4) +')';
          this.pick = item.cardSeq;
          this.selectCard = {
            cardNo: item.cardNo,
            cardSeq: item.cardSeq
          }
        },

        SelInfo: function () {
          if(!this.selOptions.length){
            MessageBox('提示', '您暂未绑卡，请到首页进行绑卡操作哦～');
            return;
          }
          this.overlay();
        },
        overlay: function () {
          var e1 = document.getElementById('modal-overlay');
          e1.style.visibility = (e1.style.visibility == "visible")? "hidden" : "visible";
        },

        validTest: function () {
          var testForm = true;

          if(this.pick == ''){
            DbsAlert.show({
              title: '提示',
              text: '请选择缴费方式'
            });
            return testForm = false;
          }

          return testForm;
        }
      },

      created: function(){
        this.signedArr = JSON.parse(sessionStorage.getItem('payNumArr'));
        //我的卡列表
        var that = this;
        this.$post(this.$urlAll.bank_card_list, {userid: window.sessionStorage.getItem('userId')}).then(function (response) {
          that.selOptions = response.t;
          if(!that.selOptions.length){
            MessageBox('提示', '您暂未绑卡，请到首页进行绑卡操作哦～');
          }

        })
      }


    }
</script>

<style scoped>
  @import "../../style/common.css";

  select{
    direction: rtl;
  }
  .signedList{
    height: 0.44rem;
    background-color: #fff;
    padding-left: 0.1rem;
  }
  .signedListInfo{
    border-bottom: 1px solid #eee;
    width: 100%;
    position: relative;
  }
  .signedList span{
    line-height: 0.44rem;
    font-size: 0.14rem;
  }
  .ListInfoR{
    padding-right: 0.1rem;
    font-size: 0.13rem;
    color: #666;
    position: absolute;
    right: 0px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);

  }
  .signedBtn{
    width: 2.93rem;
    height: 0.44rem;
    border: none;
    background-color: #999;
    font-size: 0.16rem;
    color: #fff;
    -webkit-border-radius: 0.05rem;
    -moz-border-radius: 0.05rem;
    border-radius: 0.05rem;
    outline: none;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    margin-top: 0.6rem;
  }







  .applyList{
    height: 0.44rem;
    background-color: #fff;
    padding-left: 0.1rem;
  }
  .applyListInfo{
    border-bottom: 1px solid #eee;
    width: 100%;
    position: relative;
  }
  .applyList span{
    line-height: 0.44rem;
    font-size: 0.14rem;
  }
  .ListInfoR{
    padding-right: 0.1rem;
    font-size: 0.13rem;
    color: #666;
    position: absolute;
    right: 0px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);

  }
  .contect{
    width: 1.05rem;
    height: 100%;
    border: none;
    position: absolute;
    right: 0.1rem;
    line-height: 0.44rem;
  }

  .icon-sel {
    font-size: 0.35rem;
    position: absolute;
    right: 0.1rem;
    top: 50%;
    -webkit-transform: translateY(-47%);
    -moz-transform: translateY(-47%);
    -ms-transform: translateY(-47%);
    -o-transform: translateY(-47%);
    transform: translateY(-47%);
    color: #666;
  }
  .listSel{
    right: 0.35rem;
  }

  .signedBtn{
    width: 2.93rem;
    height: 0.44rem;
    border: none;
    background-color: #ff4444;
    font-size: 0.16rem;
    color: #fff;
    -webkit-border-radius: 0.05rem;
    -moz-border-radius: 0.05rem;
    border-radius: 0.05rem;
    outline: none;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
