<template>

  <div class="hello">
    <div class="row" @click="changePswd">
      <i class="icon iconfont icon-denglumima"></i>
          <div class="col wordlsInfo">
             登录密码管理
          </div>
          <div class="col tr">
            <i class="icon iconfont  icon-shangyiye-copy"></i>
          </div>
    </div>

    <div class="row" @click="resetPwd">
      <i class="icon iconfont icon-zhifumima"></i>
        <div class="col wordlsInfo">
          支付密码管理
        </div>
        <div class="col tr">
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </div>
    </div>


  </div>
</template>

<script>

  import urlAll from '../../js/kj_url'

  export default {
    data () {
      return {

      }
    },
    methods: {
      changePswd: function () {
        window.location.href = this.$urlAll.change_login_pass+ 'clientid='+this.$urlAll.clientid+'&accesstoken='+sessionStorage.getItem('token')+
          '&userid='+sessionStorage.getItem('userId')+'&themeid=1';
      },
      resetPwd: function () {
        window.location.href = this.$urlAll.get_pay_pwd+ 'clientid='+this.$urlAll.clientid+'&accesstoken='+sessionStorage.getItem('token')+
          '&userid='+sessionStorage.getItem('userId')+'&headerbar=1&wpRedirect=';
      }
    },
    //获取后台数据，dom渲染之前
    created: function(){

    },
    //渲染dom
    mounted: function () {

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../style/common.css";
  .hello .row:first-child{
     border-bottom: 1px solid #eee;
  }
  .row{
     height: 0.43rem;
    line-height: 0.43rem;
    background: #fff;
    /*padding: 0 0.1rem;*/
    padding: 0 0.1rem 0 0.17rem;
  }

  .icon-shangyiye-copy{
    font-size: 0.3rem;
    color: #c9c9c9;
  }
  .icon-denglumima{
    width: 0.25rem;
    height: 0.25rem;
    line-height:0.25rem;
    text-align: center;
    font-size: 0.14rem;
    color: #FFFFFF;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    background-color: #00AAEE;
    margin-top: 0.09rem;
  }
.icon-zhifumima{
  width: 0.25rem;
  height: 0.25rem;
  line-height:0.25rem;
  text-align: center;
  font-size: 0.14rem;
  color: #FFFFFF;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  background-color: #00c192;
  margin-top: 0.09rem;
}
.wordlsInfo{
  position: absolute;
  left: 0.52rem;
}
</style>



