<template id="HelloWorld">
  <div class="hello">
    <div>
      <ul class="myPay-list">
        <li class="myPay-item row" @click="goPage('/pay/waitPay')">
          <i class="icon iconfont icon-daijiaofei"></i>
          <span class="col_333">待缴费</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
        <li class="myPay-item row" @click="goPage('/pay/paidFee')">
          <i class="icon iconfont icon-yijiaofei"></i>
          <span class="col_333">已缴费</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
        <li class="myPay-item row"  @click="goPage('/mine/myInfo_payForOther')">
          <i class="icon iconfont icon-aixin"></i>
          <span class="col_333">为他人缴费</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
        <li class="myPay-item row" @click="goPage('/pay/paySign')">
          <i class="icon iconfont icon-qiandao"></i>
          <span class="col_333">缴费项目签约管理</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

  export default {
    data () {
      return {

      }
    },
    methods: {
      goPage: function (url) {
        this.$router.push({path: url});
      }
    },
    //获取后台数据，dom渲染之前
    created: function(){

    },
    //渲染dom
    mounted: function () {

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../style/common.css";
  .myPay-list .myPay-item{
    height: 0.44rem;
    background: #FFFFFF;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.1rem 0 0.17rem;
  }
  .myPay-list .myPay-item:nth-child(odd){
    margin-top: 0.1rem;
    border-bottom:0.01rem solid #EEEEEE;
  }
  .myPay-list .myPay-item .iconfont{
    width: 0.25rem;
    height: 0.25rem;
    line-height:0.25rem;
    text-align: center;
    font-size: 0.18rem;
    color: #FFFFFF;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
  }
  .myPay-list .myPay-item .iconfont.icon-daijiaofei{
    background: #00aaee;
  }
  .myPay-list .myPay-item .iconfont.icon-yijiaofei{
    background: #00c192;
  }
  .myPay-list .myPay-item .iconfont.icon-aixin{
    background: #ff4444;
  }
  .myPay-list .myPay-item .iconfont.icon-qiandao{
    background: #00aaee;
  }
  .myPay-list .myPay-item .iconfont.icon-shangyiye-copy{
    font-size: 0.3rem;
    color: #c9c9c9;
  }
  .myPay-list .myPay-item span:nth-child(2){
    position: absolute;
    left: 0.52rem;
    line-height: 0.44rem;
  }
</style>



