<template id="setting">
  <div class="setting">
    <div>
      <ul class="mySetting-list">
        <li class="mySetting-item row" @click="goPage('/mine/myInfo_info')">
          <i class="icon iconfont icon-gerenxinxi"></i>
          <span class="col_333">个人信息</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
        <li class="mySetting-item row" @click="goPage('/mine/passWord_type')">
          <i class="icon iconfont icon-mima"></i>
          <span class="col_333">修改密码</span>
          <i class="icon iconfont  icon-shangyiye-copy"></i>
        </li>
        <!--<li class="mySetting-item row">-->
          <!--<i class="icon iconfont icon-iconfontnotification2"></i>-->
          <!--<span class="col_333">手势密码</span>-->
          <!--<div id="out-btn" class="out-close">-->
            <!--<div id="inner-btn" class="inner-close" @click="handleClick"></div>-->
          <!--</div>-->
        <!--</li>-->
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {

      }
    },
    methods: {
      goPage: function (url) {
        this.$router.push({path: url});
      },
      handleClick:function(){
        var innerBtn = $("#inner-btn")[0];
        var outBtn = $("#inner-btn").parent()[0];
        innerBtn.className = (innerBtn.className=="inner-close") ? "inner-active":"inner-close";
        outBtn.className = (outBtn.className=="out-close") ? "out-active":"out-close";
      }
    },
    //获取后台数据，dom渲染之前
    created: function(){

    },
    //渲染dom
    mounted: function () {

    }
  }


</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../style/common.css";
  .mySetting-list .mySetting-item{
    height: 0.44rem;
    background: #FFFFFF;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.1rem 0 0.17rem;
  }
  .mySetting-list .mySetting-item:nth-child(odd){
    margin-top: 0.1rem;
    border-bottom:0.01rem solid #EEEEEE;
  }
  .mySetting-list .mySetting-item .iconfont{
    width: 0.25rem;
    height: 0.25rem;
    line-height:0.25rem;
    text-align: center;
    font-size: 0.14rem;
    color: #FFFFFF;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
  }
  .mySetting-list .mySetting-item .iconfont.icon-mima{
    background: #00c192
  }
  .mySetting-list .mySetting-item .iconfont.icon-gerenxinxi{
    ;
    background: #00aaee;

  }
  .mySetting-list .mySetting-item .iconfont.icon-iconfontnotification2{
    background: #ff4444;
  }
  .mySetting-list .mySetting-item .iconfont.icon-shangyiye-copy{
    font-size: 0.3rem;
    color: #c9c9c9;
  }
  .mySetting-list .mySetting-item span:nth-child(2){
    position: absolute;
    left: 0.52rem;
    line-height: 0.44rem;
  }
  #out-btn{
    width: 0.375rem;
    height: 0.23rem;
    border-radius: 50px;
    position: relative;
  }
  #inner-btn{
    width: 0.21rem;
    height: 0.21rem;
    border-radius: 50%;
    position: absolute;
    background: white;
    box-shadow: 0 0.01rem 0.02rem rgba(0,0,0,0.4);
  }
  .out-close{
    background: rgba(222,222,222,0.4);
  }
  .inner-close{
    top: 0.01rem;
    left: 0.005rem;
  }
  .out-active{
    background: rgba(0,184,0,0.8);
  }
  .inner-active{
    top: 0.01rem;
    right: 0.005rem;
  }
</style>



