

// 金钱格式化
let formatNumber = function (number) {
  number = number.toString().replace(/\$|\,/g, '');

  // 1、检查传入数值为数值类型
  if (isNaN(number))
    number = '0';
  // 2、获取符号(正/负数)
  var sign = (number == (number = Math.abs(number)));//Math.abs(number)取出number的绝对值

  // 3、把指定的小数位先转换成整数.多余的小数位四舍五入，Math.pow(10, cent)为10的cent次方
  number = Math.floor(number * Math.pow(10, 2) + 0.5);
  // 4、求出小数位数值
  var cents = number % Math.pow(10, 2);
  // 5、求出整数位数值
  number = Math.floor(number / Math.pow(10, 2)).toString();
  // 6、把小数位转换成字符串,以便求小数位长度
  cents = cents.toString();

  // 7、补足小数位到指定的位数
  while (cents.length < 2)
    cents = "0" + cents;
  // 8、对整数部分进行千分位格式化.
  for (var i = 0; i < Math.floor((number.length - (1 + i)) / 3) ; i++)
    number = number.substring(0, number.length - (4 * i + 3)) + ',' + number.substring(number.length - (4 * i + 3));
  //9、返回格式化转换后的结果
  return (((sign) ? '' : '-') + '￥' + number + '.' + cents);
} ;

// 姓名前面带*号，最后面显示文字
let replace_name = function (str) {
  return new Array(str.length).join('*') + str.substr(-1);
} ;

export { formatNumber, replace_name }



