  'validate_message': host_local_url + 'app/admin/validatemessage',
  //用户附加信息查询
  'user_info': host_local_url + 'app/admin/user/userinfo',


  // 总行公共h5页面
  'bank_login': config.bank_login,
  'bank_card': config.bank_card,
  'change_login_pass': config.change_login_pass,
  'get_pay_pwd': config.get_pay_pwd,





  //本地数据接口访问
  //查询城市
  'query_city': host_local_url + 'app/admin/QueryCity',
  //查询已签约项目名称
  'sign_project': host_local_url + 'app/admin/QuerySignProject',
  //查询缴费编号详情
  'sign_project_detail': host_local_url+ 'app/admin/QuerySignProjectDetail',
  //待交费用列表查询
  'query_delay_payment': host_local_url+ 'app/admin/QueryDelayPayment',
