  'query_notity' : host_local_url + 'app/admin/getNReadNotifyList',
  //我的消息明细
  'get_notify_list': host_local_url + 'app/admin/getNotifyList',
  //未读到已读的状态转变
  'update_read': host_local_url + 'app/admin/updateNotifyRead',
