<template>
  <div class="payList">
    <!--收费项目-->
    <div class="pay-content">

      <ul class="pay-list" v-if="items.length>0">
        <li class="pay-item bg_ff" v-for="item in items" :key="item.projectId"
            @click="goPage('/pay/payParty_num', item)">
          {{item.projectName}}
        </li>
      </ul>
      <div v-if="items.length==0">
        该缴费地区下没有缴费项目
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "payparty_list",
      data () {
        return {
          items:[]
        }
      },
      methods: {
        goPage: function (url, list) {
          this.$router.push({path: url, query: list});
        }
      },
      //获取后台数据，dom渲染之前
      created: function(){
        var that = this;
        //为他人缴费接口接口
        this.$post(this.$urlAll.query_member_ship_project, {
          corpId:this.$route.query.id,
          openAccount:sessionStorage.getItem('userId')}).then(function (response) {
          that.items = response.t;
        })
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../style/common.css";
  .pay-list{
    margin-top: 0.1rem;
  }
  .pay-list .pay-item{
    height: 0.44rem;
    line-height: 0.44rem;
    font-size: 0.16rem;
    color: #333333;
    padding-left: 0.1rem;
    margin-bottom: 0.01rem;
  }

</style>



