<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <router-view/>

    <div id="drag">
      <ul class="drag_child">
        <!--<li class="main_li_first"></li>-->
        <!--<li class="main_li mainPage" ng-click="goPage('mainPage');$event.stopPropagation();"></li>-->
        <!--<li class="main_li mineProperty" ng-click="goPagebyMobileLogin('myProperty');$event.stopPropagation();"></li>-->
        <!--<li class="main_li minePage" ng-click="goPage('mine');$event.stopPropagation();"></li>-->
        <!--<li class="main_li_last"></li>-->
        <li class="main_li_first"></li>
        <li class="main_li mainPage" @click.stop="goPage('/')"></li>
        <li class="main_li mineProperty" @click.stop="goPage('/mine/myNew')"></li>
        <li class="main_li minePage" @click.stop="goPage('/mine/myInfo')"></li>
        <li class="main_li_last"></li>
      </ul>
      <div class="drag_child drag_bg" style="width:100%;height:100%;"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  methods: {
    goPage: function (url) {
      // dragToggle('close');
      if(window.sessionStorage.getItem('userId') || url == '/'){
        this.$router.push({path: url});
      } else {
        window.location.href = this.$urlAll.bank_login+ 'clientid='+ this.$urlAll.clientid +'&clientSecret='+ this.$urlAll.clientSecret +
          '&isBranches=true&encrypt_type=raw&wpRedirect='+ this.$urlAll.indexHref;
      }

    }
  },
  mounted: function () {
    // 初始化浮动图标
    var Drag = new DBSDrag('drag');
  }
}
</script>


<style>
  @import "style/common.css";
  @import "assets/css/iconfont.css";
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f3f3f3;
}

  .mint-msgbox-message{
    max-height: 2rem;
    overflow-y: auto;
  }

  /*图片圆形的*/
  .border-radious-img{
    border-radius: 50%;
    height: 0.4rem;
  }
</style>



